html {
  --primary: rgb(11, 155, 100);
  --secondary: rgba(255, 253, 253, 0.753);
  --secondar-gray: rgba(153, 153, 153, 0.753);
  --black: rgb(24, 16, 29);
  --white: #fff;
  height: 100%;
}

body {
  background: var(--white);
  height: 100%;
}

#root {
  height: 100%;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: var(--black);
}
.player{
  padding: 10px;
  width: 470px;
  height: 365px;
  display: flex;
  justify-content: space-between;
  background: var(--white);
}

.player .list {
  position: relative;
  width: 140px;
  height: 95%;
  padding: 10px;
  color: var(--primary);
  overflow: hidden;
}

.player .list div {
  color: var(--black);
  opacity: 0.8;
  font-weight: bold;
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  height: 180px;
  width: 120px;
  margin: auto;
}

.player .list p.played {
  animation: 10s linear .2s infinite reverse both played;
}

.player .illustration {
  width: 300px;
}
.player .illustration .music {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  background-color: var(--black);
  opacity: 0.8;
}

.player .illustration .console {
  position: relative;
  height: 50px;
  width: 100%;
  margin-top: 15px;
}

.console .buttons {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  width: 40%;
  justify-content: space-between;
}

.music .add_music_icon {
  position: relative;
  background: var(--white);
  float: right;
  height: 30px;
  width: 30px;
  margin-top: 10px;
  margin-right: 10px;
  border-radius: 50%;
  color: var(--primary);
  transition: transform .2s cubic-bezier(0.68, -0.55, 0.265, 1.55), background .3s cubic-bezier(0.23, 1, 0.320, 1), color .3s ease-in;
  cursor: pointer;
}

.music .call-to-add-music .add_music_icon {
  animation: 1s ease-in infinite alternate callToAddMusic; 
}

.music .add_music_icon:hover {
  background:  var(--primary);
  color: var(--secondary);
  transform: scale(1.1);
}

.add_music_icon .feather-music, 
.add_music_icon .feather-plus {
  position: absolute;
}

.add_music_icon .feather-music {
  left: 0;
  right: 2px;
  top: 0;
  bottom: 0;
  margin: auto;
  transform: scale(0.7);
}

.add_music_icon .feather-plus {
  transform: scale(0.6);
  right: -5px;
  top: -1px;
}

.music .disc {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  transform: scale(0.6);
  fill: var(--secondar-gray);
}

.music .disc.spin {
  width: 185px;
  height: 185px;
  animation: 10s linear .2s infinite running spin;
}

.play_icon, 
.pause_icon, 
.stop_icon, 
.prev_icon, 
.next_icon {
  fill: var(--primary);
  width: 25px;
  cursor: pointer;
  transition: transform .3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.stop_icon, .pause_icon {
  width: 20px;
}

.play_icon:hover, 
.pause_icon:hover, 
.stop_icon:hover,
.prev_icon:hover,
.next_icon:hover {
  transform: scale(1.1);
}

@keyframes callToAddMusic {
  from {
    box-shadow: none;
    transform: none;
  }

  to {
    box-shadow: 0 0 10px var(--primary);
    transform: scale(1.2);
  }
}

@keyframes spin {
  from {
    transform: none;
  }

  to {
    transform: rotate(360deg)
  }
}

@keyframes played {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(100%);
  }
}
